import type { FileNode } from './fileSystem';

/**
 * Creates a TypeScript project template structure
 * Based on https://github.com/bscotch/typescript-template
 */
export function createTypeScriptTemplate(projectName: string): FileNode[] {
  const generateId = () => crypto.randomUUID();

  return [
    // package.json
    {
      id: generateId(),
      name: 'package.json',
      type: 'file',
      content: `{
  "name": "${projectName.toLowerCase().replace(/\s+/g, '-')}",
  "description": "A TypeScript project created with WebCode Studio",
  "repository": "",
  "homepage": "",
  "author": "",
  "keywords": [],
  "private": true,
  "version": "0.0.1",
  "engines": {
    "node": ">=13.2.0"
  },
  "main": "./build/index.js",
  "files": [
    "build/lib/**/*",
    "build/index.*",
    "build/types/**/*"
  ],
  "scripts": {
    "test": "npm run build && mocha --inspect --require source-map-support/register --bail ./build/test/",
    "build": "rimraf build && tsc && tsc-alias",
    "preversion": "npm run lint && npm run build && npm test",
    "version": "conventional-changelog -p angular -i CHANGELOG.md -s -r 0 && npm run format && git add -A",
    "postversion": "git push --follow-tags && npm publish",
    "lint": "eslint --ext ts,js --fix src",
    "format": "prettier src/**/*.ts -w",
    "build-live": "tsc -w && tsc-alias -w"
  },
  "license": "MIT",
  "dependencies": {
    "commander": "^7.2.0"
  },
  "devDependencies": {
    "@types/chai": "^4.2.18",
    "@types/fs-extra": "^9.0.11",
    "@types/mocha": "^8.2.2",
    "@types/node": "^15.6.0",
    "@typescript-eslint/eslint-plugin": "^4.25.0",
    "@typescript-eslint/parser": "^4.25.0",
    "chai": "^4.3.4",
    "conventional-changelog-cli": "^2.1.1",
    "eslint": "^7.27.0",
    "eslint-plugin-prettier": "^3.4.0",
    "fs-extra": "^10.0.0",
    "mocha": "^8.4.0",
    "prettier": "^2.3.0",
    "rimraf": "^3.0.2",
    "source-map-support": "^0.5.19",
    "tsc-alias": "^1.2.11",
    "typescript": "next"
  }
}`,
    },
    
    // tsconfig.json
    {
      id: generateId(),
      name: 'tsconfig.json',
      type: 'file',
      content: `{
  "include": ["./src"],
  "exclude": ["node_modules"],
  "compilerOptions": {
    "target": "es2017",
    "module": "commonjs",
    "lib": ["es2020"],
    "outDir": "./build",
    "declaration": true,
    "declarationMap": true,
    "sourceMap": true,
    "moduleResolution": "node",
    "allowSyntheticDefaultImports": true,
    "esModuleInterop": true,
    "strict": true,
    "checkJs": true,
    "allowJs": true,
    "skipLibCheck": true,
    "baseUrl": ".",
    "paths": {
      "@/*": ["src/lib/*"],
      "$/*": ["src/*"],
      "$cli/*": ["src/cli/*"],
      "$test/*": ["src/test/*"]
    }
  }
}`,
    },

    // README.md
    {
      id: generateId(),
      name: 'README.md',
      type: 'file',
      content: `# ${projectName}

A TypeScript project created with WebCode Studio.

## Getting Started

### Installation

\`\`\`bash
npm install
\`\`\`

### Build

\`\`\`bash
npm run build
\`\`\`

### Test

\`\`\`bash
npm test
\`\`\`

### Development

\`\`\`bash
npm run build-live
\`\`\`

## Project Structure

- \`src/\` - Source files
  - \`lib/\` - Library modules
  - \`cli/\` - CLI commands
  - \`test/\` - Test files
  - \`types/\` - TypeScript type definitions
- \`build/\` - Compiled output (generated)

## Features

- ðŸš€ Modern TypeScript setup
- ðŸ“¦ Ready for npm publishing
- ðŸ§ª Test suite with Mocha
- ðŸ’» CLI support with Commander
- âœ¨ Code formatting with Prettier
- ðŸ”¬ Linting with ESLint

## License

MIT`,
    },

    // .gitignore
    {
      id: generateId(),
      name: '.gitignore',
      type: 'file',
      content: `node_modules
build
sandbox
sand\\ box
debug.log
*.log
.DS_Store
.env
.env.local`,
    },

    // .prettierrc
    {
      id: generateId(),
      name: '.prettierrc',
      type: 'file',
      content: `{
  "trailingComma": "all",
  "bracketSpacing": true,
  "tabWidth": 2,
  "semi": true,
  "singleQuote": true
}`,
    },

    // .eslintrc
    {
      id: generateId(),
      name: '.eslintrc',
      type: 'file',
      content: `{
  "env": {
    "es6": true,
    "node": true,
    "mocha": true
  },
  "extends": [
    "eslint:recommended",
    "plugin:@typescript-eslint/recommended"
  ],
  "parser": "@typescript-eslint/parser",
  "plugins": [
    "@typescript-eslint",
    "prettier"
  ],
  "parserOptions": {
    "ecmaVersion": 2019,
    "sourceType": "module",
    "project": "./tsconfig.json"
  },
  "ignorePatterns": [
    ".eslintrc.js",
    "prettier.config.js"
  ],
  "rules": {
    "@typescript-eslint/ban-ts-ignore": "off",
    "@typescript-eslint/type-annotation-spacing": "off",
    "@typescript-eslint/explicit-function-return-type": "off",
    "no-unused-vars": "off",
    "@typescript-eslint/no-unused-vars": [
      "warn",
      {
        "args": "none"
      }
    ],
    "@typescript-eslint/adjacent-overload-signatures": "error",
    "@typescript-eslint/ban-types": "error",
    "camelcase": "off",
    "@typescript-eslint/consistent-type-assertions": "error",
    "no-array-constructor": "off",
    "@typescript-eslint/no-array-constructor": "error",
    "no-empty": "off",
    "no-empty-function": "off",
    "@typescript-eslint/explicit-module-boundary-types": "off",
    "@typescript-eslint/no-empty-function": "error",
    "@typescript-eslint/no-empty-interface": "error",
    "@typescript-eslint/no-explicit-any": "off",
    "@typescript-eslint/no-inferrable-types": "error",
    "@typescript-eslint/no-misused-new": "error",
    "@typescript-eslint/no-namespace": "error",
    "@typescript-eslint/no-non-null-assertion": "off",
    "@typescript-eslint/no-this-alias": "error",
    "no-use-before-define": "off",
    "@typescript-eslint/no-use-before-define": "off",
    "@typescript-eslint/no-var-requires": "error",
    "@typescript-eslint/prefer-namespace-keyword": "error",
    "@typescript-eslint/triple-slash-reference": "error",
    "@typescript-eslint/no-floating-promises": [
      "error"
    ],
    "no-var": "error",
    "prefer-const": "error",
    "prefer-rest-params": "error",
    "prefer-spread": "error"
  },
  "overrides": [
    {
      "files": [
        "*.js"
      ],
      "rules": {
        "@typescript-eslint/no-var-requires": "off"
      }
    }
  ]
}`,
    },

    // .eslintignore
    {
      id: generateId(),
      name: '.eslintignore',
      type: 'file',
      content: `build
node_modules`,
    },

    // .prettierignore
    {
      id: generateId(),
      name: '.prettierignore',
      type: 'file',
      content: `build
node_modules`,
    },

    // src folder
    {
      id: generateId(),
      name: 'src',
      type: 'folder',
      children: [
        // src/index.ts
        {
          id: generateId(),
          name: 'index.ts',
          type: 'file',
          content: `/**
 * @file This is the entrypoint for your project.
 * If used as a node module, when someone runs
 * \`import stuff from 'your-module'\` (typescript)
 * or \`const stuff = require('your-module')\` (javascript)
 * whatever is exported here is what they'll get.
 * For small projects you could put all your code right in this file.
 */

export * from './lib/sample-module.js';
export default undefined;`,
        },

        // src/lib folder
        {
          id: generateId(),
          name: 'lib',
          type: 'folder',
          children: [
            {
              id: generateId(),
              name: 'sample-module.ts',
              type: 'file',
              content: `/**
 * @file ./lib is a great place to keep all your code.
 * You can then choose what to make available by default by
 * exporting your lib modules from the ./src/index.ts entrypoint.
 */

export function greet(name: string): string {
  return \`Hello, \${name}!\`;
}

export class SampleClass {
  private name: string;

  constructor(name: string) {
    this.name = name;
  }

  getName(): string {
    return this.name;
  }

  setName(name: string): void {
    this.name = name;
  }
}

export default undefined;`,
            },
          ],
        },

        // src/types folder
        {
          id: generateId(),
          name: 'types',
          type: 'folder',
          children: [
            {
              id: generateId(),
              name: 'index.ts',
              type: 'file',
              content: `/**
 * @file Type definitions for your project.
 * Keep all your TypeScript interfaces and types here.
 */

export interface User {
  id: string;
  name: string;
  email: string;
}

export type Status = 'pending' | 'active' | 'completed';

export default undefined;`,
            },
          ],
        },

        // src/test folder
        {
          id: generateId(),
          name: 'test',
          type: 'folder',
          children: [
            {
              id: generateId(),
              name: 'index.ts',
              type: 'file',
              content: `/**
 * @file Test suite for your project.
 * Add your tests here using Mocha and Chai.
 */

import { expect } from 'chai';
import { greet, SampleClass } from '../lib/sample-module.js';

describe('Sample Module', () => {
  describe('greet()', () => {
    it('should return a greeting message', () => {
      const result = greet('World');
      expect(result).to.equal('Hello, World!');
    });
  });

  describe('SampleClass', () => {
    it('should create an instance with a name', () => {
      const instance = new SampleClass('Test');
      expect(instance.getName()).to.equal('Test');
    });

    it('should allow name to be changed', () => {
      const instance = new SampleClass('Test');
      instance.setName('NewTest');
      expect(instance.getName()).to.equal('NewTest');
    });
  });
});`,
            },
          ],
        },

        // src/cli folder
        {
          id: generateId(),
          name: 'cli',
          type: 'folder',
          children: [
            {
              id: generateId(),
              name: 'cli.ts',
              type: 'file',
              content: `/**
 * @file CLI entrypoint for your project.
 * Use Commander.js to create command-line interfaces.
 */

import { Command } from 'commander';
import { greet } from '../lib/sample-module.js';

const program = new Command();

program
  .name('${projectName.toLowerCase().replace(/\s+/g, '-')}')
  .description('CLI for ${projectName}')
  .version('0.0.1');

program
  .command('greet <name>')
  .description('Greet someone')
  .action((name: string) => {
    console.log(greet(name));
  });

program.parse();`,
            },
          ],
        },
      ],
    },
  ];
}
